* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
}
.app {
  background-image: url('Data/tumblr_odnrvedGW31qhttpto1_540.jpg');
  background-size: cover;
  background-position: center;
  transition: 0.7s ease-out;
}
.app.warm {
  background-image: url('Data/peakpx.jpg');
}
main {
  min-height: 100vh;
  background-image: linear-gradient(to bottom, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.8));
  padding: 25px;
}
.search-box {
  width: 100%;
  margin: 0 0 75px;
}
.search-box .search-bar{
  display: block;
  width: 100%;
  padding: 15px;
  max-width: 450px;

  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.589);
  border-radius: 0px 0px 16px 16px;
  margin-top: -25px;
  margin-left: auto;
  margin-right: auto;

  box-shadow: 0px 7px rgba(0, 0, 0, 0.753);

  color:black;
  font-size: 20px;
  text-align: center;

  transition: 0.7s ease-out;
}
.search-box .search-bar:focus{
  background-color: rgba(255, 255, 255, 0.75);
}
.location-box .location {
  color: white;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 4px 4px rgba(50, 50 , 70, 0.5);
}
.location-box .date {
  color: white;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 50 , 70, 0.5);
}

.weather-box{
  text-align: center;
}
.weather-box .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 16px;

  padding: 15px 25px;

  color: white;
  font-size: 102px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 50 , 70, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}
.weather-box .weather {
  color: white;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(50, 50 , 70, 0.5);
}